*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
}

.container {
  width: min(95%, 120rem);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

figure {
  margin: 0;
}

ul {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 2rem 0;
  font-family: "Outfit", sans-serif;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  margin: 2rem 0;
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.form {
  display: flex;
  flex-direction: column;
}
.form__group {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.form__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.form__input {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.form__submit {
  width: 100%;
  padding: 1.5rem 4rem;
  display: inline-block;
  border: none;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #007df4;
  transition-property: background-color;
  transition-duration: 0.3s;
  width: auto;
  align-self: flex-start;
}
.form__submit:hover {
  background-color: #006bd0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}

.actions, .actions--center {
  display: flex;
  justify-content: space-between;
  margin-top: 3rem;
}
.actions--center {
  justify-content: center;
}
.actions__link {
  font-size: 1.4rem;
  font-weight: 300;
  color: #1a1b15;
}

.header {
  min-height: 60rem;
  background-image: url("../img/grafico.svg"), url("../img/grafico.svg"), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url("../img/header.avif") 1x, url("../img/header.webp") 1x, url("../img/header.jpg") 1x);
  background-repeat: no-repeat;
  background-size: 400px, 400px, cover, cover;
  background-position: top left, bottom right, center center, center center;
}
.header__container {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__nav {
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .header__nav {
    justify-content: flex-end;
  }
}
.header__link {
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  color: #FFFFFF;
  transition-property: color;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}
.header__link:hover {
  color: #007df4;
}
.header__content {
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .header__content {
    padding: 5rem;
  }
}
.header__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    display: inline-block;
    text-align: start;
  }
}
.header__text {
  margin: 2rem 0;
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__text {
    text-align: start;
  }
}
.header__text--mode {
  font-size: 2rem;
}
.header__button {
  width: 100%;
  padding: 1.5rem 4rem;
  display: inline-block;
  border: none;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  color: #1a1b15;
  background-color: #00c8c2;
  transition-property: background-color;
  transition-duration: 0.3s;
  margin: 2rem 0;
  text-transform: uppercase;
}
.header__button:hover {
  background-color: #00a49f;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
  }
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
  }
}

.bar {
  background-color: #007df4;
}
.bar__content {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .bar__content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.bar__logo {
  margin-bottom: 2rem;
  text-align: center;
  color: #FFFFFF;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 1024px) {
  .bar__logo {
    margin: 0;
    text-align: start;
  }
}

.nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  padding: 2rem;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
  transition-property: color background-color;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}
.nav__link:hover {
  color: #1a1b15;
  background-color: #FFFFFF;
}

.footer {
  margin-top: 5rem;
  padding-top: 3rem;
  color: #FFFFFF;
  background-color: #007df4;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.footer__logo {
  margin-bottom: 2rem;
  text-align: center;
  color: #FFFFFF;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (min-width: 1024px) {
  .footer__logo {
    margin: 0;
    text-align: start;
  }
}
.footer__text {
  line-height: 2;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__text {
    text-align: start;
  }
}
.footer__copyright {
  margin: 0;
  margin-block-start: 3rem;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
  background-color: #0063c1;
}
.footer__copyright--regular {
  font-weight: 400;
}

.socials-menu {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin: 4rem 0;
}
.socials-menu__link::before {
  display: inline-block;
  font-size: 2rem;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
  color: #FFFFFF;
}
.socials-menu__link[href*="https://facebook.com"]::before {
  content: "\f39e";
}
.socials-menu__link[href*="https://github.com"]::before {
  content: "\f09b";
}
.socials-menu__link[href*="https://tiktok.com"]::before {
  content: "\e07b";
}
.socials-menu__link[href*="https://instagram.com"]::before {
  content: "\f16d";
}
.socials-menu__link[href*="https://youtube.com"]::before {
  content: "\f167";
}
.socials-menu__link[href*="https://twitter.com"]::before {
  content: "\f099";
}
.socials-menu__hide {
  display: none;
}
@media (min-width: 768px) {
  .socials-menu {
    justify-content: flex-end;
    align-items: center;
  }
}

.alert {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  margin: 1rem 0;
  border-radius: 0.5rem;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  line-height: 2;
}
.alert__error {
  color: rgb(169, 0, 0);
  border-left: 0.5rem solid rgb(169, 0, 0);
  background-color: #ffdcdc;
}
.alert__success {
  color: #017601;
  border-left: 0.5rem solid #02db02;
  background-color: #abfeab;
}